/* SIGN_UP PAGE================================ */
.main-signup-div {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #000000;
  position: relative;
  overflow: hidden;
}

.signup-container {
  z-index: 1;
  display: flex;
  align-items: center;
  height: 100%;
}

.signup-form {
  background-color: rgba(151, 151, 151, 0.336);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 100%;
  margin: auto;
}

.signup-form h2 {
  margin-bottom: 20px;
  font-weight: 900;
  color: rgb(247, 247, 247);

  text-shadow: 2px 2px 2px rgb(29, 29, 29);
}

.signup-form button {
  font-weight: 900;
  font-size: 22px;
  color: #f0eeeb;
  background-color: rgb(78, 0, 0);

  outline: rgb(0, 0, 0);
}

.signup-form button:hover {

  outline: rgb(0, 0, 0);
  border: 1px solid white;
  
  color: #f0eeeb;
  background-color: rgba(240, 156, 0, 0);
}

.signup-form label i {
  color: rgb(87, 0, 0);

text-shadow: 2px 2px 2px rgba(134, 133, 133, 0.568);
}


.background-circle {
  position: absolute;
  border-radius: 50%;
  background: linear-gradient(to bottom, #950101 0%, #630102 50%, #3d0302 );
  /* background-color: #005f00; */
  box-shadow: 3px 5px 15px grey;
  z-index: 0;
}

.circle1 {
  width: 25vw;
  height: 25vw;
  top: -10vw;
  left: 70vw;
}

.circle2 {
  width: 50vw;
  height: 50vw;
  top: -20vw;
  left: -15vw;
}

.circle3 {
  width: 6.25vw;
  height: 6.25vw;
  bottom: 10vw;
  right: 5vw;
}

.circle4 {
  width: 3vw;
  height: 3vw;
  top: 35vw;
  left: 40vw;
}

.circle5 {
  width: 18.75vw;
  height: 18.75vw;
  bottom: -10vw;
  left: -10vw;
}

.sign-In-img {
  
  width: 100%;
  height: auto;
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.sign-up-img {
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.social-login {
  display: flex;
  justify-content: space-around;
  margin-top: 15px;
}

.social-login a {
  font-size: 18px;
  color: #fff;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}

.social-login .facebook {
  background-color: #3b5998;
}

.social-login .google {
  background-color: #db4a39;
}

.social-login .twitter {
  background-color: #55acee;
}

.login-text {
  margin-top: 20px;
  text-align: center;
  color: rgb(247, 247, 247);
  
  text-shadow: 2px 2px 2px rgb(29, 29, 29);
}
.divider{
  color: rgb(247, 247, 247);

}

.login-text a {
  color: rgb(247, 247, 247);
  text-decoration: none;
  font-weight: bold;
}

.divider {
  display: flex;
  align-items: center;
  margin: 20px 0;
}

.divider::before,
.divider::after {
  content: '';
  flex: 1;
  height: 1px;
  background: #ddd;
}

.divider:not(:empty)::before {
  margin-right: .25em;
}

.divider:not(:empty)::after {
  margin-left: .25em;
}

.signup-form p,label{
  color: rgb(247, 247, 247);

  text-shadow: 2px 2px 2px rgb(29, 29, 29);
}

.sign-in-img{
  width: 100%;
  height: auto;
}

.img-div{
  height: 600px;
  width: 50%;
}


